import { styled } from '@material-ui/core/styles'
import React from 'react'
import { TransparentCard } from './TransparentCard'

const StyledBackground = styled(({ color, ...other }) => <section {...other} />)({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  height: '100vh',
  width: '100vw',
})

const StyledCircle = styled(({ color, ...other }) => <div {...other} />)({
  position: 'fixed',
  bottom: '-10%',
  left: '50%',
  width: '600px',
  height: '600px',
  background: 'linear-gradient(#f00,#f0f)',
  borderRadius: '50%',
  zIndex: '-1',
})

export function Background() {
  return (
    <StyledBackground>
      <TransparentCard />
      <StyledCircle />
    </StyledBackground>
  )
}
