import { styled } from '@material-ui/core/styles'
import React from 'react'
import chip from './images/chip.png'
import notrealsign from './images/notrealsign.jpg'

// const cardSize = { width: 'calc(508px*(50vw/100vw))', height: 'calc(314px*(50vw/100vw))' }

const cardSize = { width: 'calc(508px )', height: 'calc(314px )' }

const StyledCard = styled(({ color, ...other }) => <div {...other} />)({
  position: 'relative',
  width: cardSize.width,
  height: cardSize.height,
  transformStyle: 'preserve-3d',
  perspective: '500px',
  transition: '1s',
  // '&:hover': {
  //   transform: 'rotateY(180deg)',
  // },
})

const StyledCardFaceFront = styled(({ color, ...other }) => <div {...other} />)({
  position: 'absolute',
  top: '0',
  left: '0',
  width: cardSize.width,
  height: cardSize.height,
  boxShadow: '0 15px 35px rgba(0,0,0,0.5)',
  borderRadius: '15px',
  background: 'rgba(255,255,255,0.05)',
  backdropFilter: 'blur(10px)',
  transformStyle: 'preserve-3d',
  transition: '1s',
  backfaceVisibility: 'hidden',
  // transform: 'rotateY(180deg)',
  // '&:hover': {
  //   transform: 'rotateY(180deg)',
  // },

  '&::before': {
    content: '""',
    position: 'absolute',
    bottom: '40px',
    right: '40px',
    width: '60px',
    height: '60px',
    background: '#fff',
    borderRadius: '50%',
    opacity: '0.5',
  },
  '&::after': {
    content: '""',
    position: 'absolute',
    bottom: '40px',
    right: '80px',
    width: '60px',
    height: '60px',
    background: '#fff',
    borderRadius: '50%',
    opacity: '0.7',
  },
})

const StyledDebit = styled(({ color, ...other }) => <h3 {...other} />)({
  position: 'absolute',
  left: '40px',
  top: '30px',
  color: '#fff',
  fontWeight: '500',
})

const StyledBankName = styled(({ color, ...other }) => <h3 {...other} />)({
  position: 'absolute',
  right: '40px',
  top: '0px',
  color: '#fff',
  fontWeight: '500',
  fontStyle: 'italic',
  fontSize: '24px',
})

const StyledChip = styled(({ color, ...other }) => <img src={chip} alt="chip" {...other} />)({
  position: 'absolute',
  left: '55px',
  top: '80px',
  maxWidth: '64px',
})

const StyledNumber = styled(({ color, ...other }) => <h3 {...other} />)({
  position: 'absolute',
  bottom: '120px',
  left: '40px',
  color: '#fff',
  fontWeight: '500',
  letterSpacing: '6px',
  fontSize: '18px',
  textShadow: '0 2px 1px #0005',
  fontFamily: '"Orbitron", sans-serif',
})

const StyledValid = styled(({ color, ...other }) => <h5 {...other} />)({
  position: 'absolute',
  bottom: '90px',
  left: '40px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  color: '#fff',
  fontWeight: '300',
  lineHeight: '1em',
  textAlign: 'right',
})

const StyledSpan = {
  marginLeft: '20px',
  fontSize: '16px',
  fontWeight: '400',
  letterSpacing: '2px',
}

const StyledHolder = styled(({ color, ...other }) => <h5 {...other} />)({
  position: 'absolute',
  bottom: '0px',
  left: '40px',
  color: '#fff',
  fontWeight: '300',
  fontSize: '30px',
  letterSpacing: '2px',
})

const StyledCardFaceBack = styled(({ color, ...other }) => <div {...other} />)({
  backdropFilter: 'blur(10px)',
  position: 'absolute',
  top: '0',
  left: '0',
  width: cardSize.width,
  height: cardSize.height,
  boxShadow: '0 15px 35px rgba(0,0,0,0.5)',
  borderRadius: '15px',
  background: 'rgba(255,255,255,0.05)',
  transformStyle: 'preserve-3d',
  transition: '1s',
  transform: 'rotateY(180deg)',
  backfaceVisibility: 'hidden',
  '&:hover': {
    transform: 'rotateY(180deg)',
  },
})

const StyledBlackBar = styled(({ color, ...other }) => <div {...other} />)({
  position: 'absolute',
  top: '40px',
  width: '100%',
  height: '60px',
  background: '#000',
})

const StyledCCVText = styled(({ color, ...other }) => <div {...other} />)({
  position: 'absolute',
  top: '100px',
  left: '30px',
})

const StyledCVV = styled(({ color, ...other }) => <h5 {...other} />)({
  color: '#fff',
  fontWeight: '400',
  fontSize: '12px',
  textTransform: 'uppercase',
  letterSpacing: '2px',
})

const StyledWhiteBar = styled(({ color, ...other }) => <div {...other} />)({
  position: 'relative',
  width: '400px',
  height: '40px',
  background: '#fff',
  marginTop: '-10px',
})

const StyledCCV = styled(({ color, ...other }) => <div {...other} />)({
  position: 'relative',
  top: '-35px',
  left: '395px',
  background: '#ccc',
  width: '50px',
  height: '30px',
  fontWeight: '600',
  letterSpacing: '3px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
})

const StyledText = styled(({ color, ...other }) => <p {...other} />)({
  position: 'absolute',
  bottom: '26px',
  left: '30px',
  right: '30px',
  color: '#fff',
  fontSize: '10px',
  lineHeight: '1.4em',
  fontWeight: '300',
  fontFamily: '"Kanit", sans-sarif',
})

const StyledNotRealSign = styled(({ color, ...other }) => (
  <img src={notrealsign} alt="chip" {...other} />
))({
  position: 'relative',
  width: '150px',
  left: '116px',
})

export function TransparentCard() {
  return (
    <StyledCard className="card">
      <StyledCardFaceFront className="front">
        <StyledDebit>Credit Card</StyledDebit>
        <StyledBankName>PNL Good Code</StyledBankName>
        <StyledChip />
        <StyledNumber>5692 8956 9289 5692</StyledNumber>
        <StyledValid>
          <span>
            Valid
            <br />
            thru
          </span>
          <span style={StyledSpan}>06/25</span>
        </StyledValid>
        <StyledHolder>Chatbordin Klinsrisuk</StyledHolder>
      </StyledCardFaceFront>

      <StyledCardFaceBack className="back">
        <StyledBlackBar />
        <StyledCCVText>
          <StyledCVV>Authorized Signature-not valid unless signed</StyledCVV>
          <StyledWhiteBar>
            <StyledNotRealSign />
          </StyledWhiteBar>
          <StyledCCV>651</StyledCCV>
        </StyledCCVText>
        <StyledText>
          วิศวกรไฟฟ้าที่เพิ่งรู้ว่าตัวเองชอบเขียนโค้ดตอนอายุ 30 ปีบริบูรณ์
          เขาเคยขายโปรแกรมได้ด้วยตัวเองครั้งแรกในราคา 39 บาทด้วยภาษา Python
          และต่อมาเขาเริ่มพัฒนาฝีมือในการเขียนเว็บด้วย NodeJS จนสามารถทำเงินได้ 78 บาท/เดือน
          ปัจจุบันกำลังฝึกเขียน ReactJS อย่างหนักหน่วง เพราะเขาตั้งใจว่าจะเป็นคนที่เขียน React
          เก่งที่สุดในไทย ฉัตรบดินทร์เป็นคนที่เชื่อว่าโชคชะตาเป็นสิ่งที่ถูกกำหนดมาแล้ว
          แต่เราสามารถกำหนดมันใหม่ได้เสมอ และเมื่อไม่นานมานี้
          ศาสตร์แห่งตัวเลขได้ทำให้เขามีความเชื่อในพุทธศาสนาอย่างหาที่สุดไม่ได้
        </StyledText>
      </StyledCardFaceBack>
    </StyledCard>
  )
}
