import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Backdrop from '@material-ui/core/Backdrop'
import SpeedDial from '@material-ui/lab/SpeedDial'
import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon'
import SpeedDialAction from '@material-ui/lab/SpeedDialAction'
import FileCopyIcon from '@material-ui/icons/FileCopyOutlined'
import SaveIcon from '@material-ui/icons/Save'
import PrintIcon from '@material-ui/icons/Print'
import ShareIcon from '@material-ui/icons/Share'
import FavoriteIcon from '@material-ui/icons/Favorite'
import MenuIcon from '@material-ui/icons/Menu'
import HomeIcon from '@material-ui/icons/Home'
import SportsHandballIcon from '@material-ui/icons/SportsHandball'
import NextWeekIcon from '@material-ui/icons/NextWeek'
import MailIcon from '@material-ui/icons/Mail'

const useStyles = makeStyles((theme) => ({
  root: {
    height: 380,
    transform: 'translateZ(0px)',
    flexGrow: 1,
    position: 'absolute',
  },
  speedDial: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}))

const actions = [
  { icon: <HomeIcon style={{ color: '#1c1c25' }} />, name: 'HOME' },
  { icon: <SportsHandballIcon style={{ color: '#1c1c25' }} />, name: 'SKILLS' },
  { icon: <NextWeekIcon style={{ color: '#1c1c25' }} />, name: 'MY WORKS' },
  { icon: <MailIcon style={{ color: '#1c1c25' }} />, name: 'CONTACT' },
]

export default function SpeedDialTooltipOpen() {
  const [open, setOpen] = React.useState(false)

  const handleOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <SpeedDial
      ariaLabel="SpeedDial tooltip example"
      hidden={false}
      icon={<MenuIcon />}
      onClose={handleClose}
      onOpen={handleOpen}
      open={open}
      direction={'down'}
      FabProps={{ style: { background: 'none' } }}
    >
      {actions.map((action) => (
        <SpeedDialAction
          key={action.name}
          icon={action.icon}
          tooltipTitle={<span>{action.name}</span>}
          tooltipOpen={false}
          onClick={handleClose}
          tooltipPlacement={'right'}
        />
      ))}
    </SpeedDial>
  )
}
